  .productSlider .swiper {
    width: 100%;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
  }

  .productSlider .swiper-slide {
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    background: #fff;
}



 .productSlider .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.4;
}

.productSlider .swiper-slide-thumb-active {
    opacity: 1;
    border: 1px solid lightgray;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  